import React from 'react';
import ReactDOM from 'react-dom/client';
import './styles.css';
import App from './App';

// Register service worker
if ('serviceWorker' in navigator) {
  window.addEventListener('load', async () => {
    try {
      const registration = await navigator.serviceWorker.register('/sw.js', {
        scope: '/'
      });
      
      // Check for updates every hour instead of on every load
      setInterval(() => {
        registration.update();
      }, 60 * 60 * 1000);

      // Handle updates more gracefully
      registration.addEventListener('updatefound', () => {
        const newWorker = registration.installing;
        if (newWorker) {
          newWorker.addEventListener('statechange', () => {
            if (newWorker.state === 'installed' && navigator.serviceWorker.controller) {
              // New content is available, but we'll wait for user interaction
              const reloadConfirm = window.confirm(
                'New version available! Would you like to update? (Reload required)'
              );
              if (reloadConfirm) {
                window.location.reload();
              }
            }
          });
        }
      });

      console.log('SW registered: ', registration);
    } catch (error) {
      console.error('SW registration failed: ', error);
    }
  });
} else {
  console.warn('Service workers are not supported in this browser');
}

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
); 